.ul {
  list-style: none;
  margin: 0;
  padding: 30px 0 30px 140px;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.li {
  display: flex;
  justify-content: start;
  margin-right: 100px;
  width: 100%;
}

.h1 {
  color: black;
}

.container {
  display: flex;
}

@media (max-width: 800px) {
  .ul {
    padding: 30px 0 30px 60px;
    width: 100%;
  }
}
